@import 'custom/variables';

// 5.1.3
// @import 'bootstrap/bootstrap';

@import '~bootstrap/scss/bootstrap';

@import 'custom/mixins';

@import "./fontawesome/scss/fontawesome.scss";
@import "./fontawesome/scss/solid.scss";
// @import "./fontawesome/scss/regular.scss";
@import "./fontawesome/scss/brands.scss";

*,
*::before,
*::after {
    cursor: none !important;
    // border: 1px solid red !important;
}

::selection {
    background: $primary;
    color: $light;
}

[data-setbg] {
    background-position: center !important;
    background-repeat: no-repeat !important;
    background-size: cover !important;
}

a {
    text-decoration: none;
    color: currentColor;
}

body {
    font-family: "Source Sans Pro", $font-fallback;
    overflow: hidden;

    &.scrollable {
        overflow: hidden auto;
    }
}

img {
    max-width: 100%;
    height: auto;
}

// @import 'splide';

header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 100;
    background: $wlnavy;
    // height: var(--header-h);
    // backdrop-filter: blur(10px);
    transition: background .5s $easing;

    &.sticky {
        background: $primary;
        box-shadow: 0 2px 10px rgba(#000, .35);
    }
}

.container {
    // max-width: 100vw;
    margin: auto;
}

.row {
    max-width: 100vw;
    margin-left: auto;
    margin-right: auto;
}

.preloader {
    position: fixed;
    @include wh100;
    top: 0;
    left: 0;
    background: $primary;
    z-index: 1000;
    pointer-events: none;

    .container {
        height: 100%;
    }

    .logo-container {
        @include wh100;
        @include flexCenter;
    }

    .wl-logo {
        @include wh(500px);
        max-width: 80%;
        flex: 0 0 auto;
    }
}

main {
    padding-top: 80px;
}

.navbar {
    position: relative;
    height: 100%;

    @media (max-width: 991px) {
        > .container-fluid {
            background: $primary;
        }
    }

    &-nav {
        gap: 20px;

        @include media-breakpoint-down(lg) {
            max-height: 80vh;
            overflow-y: auto;
            overflow-x: hidden;
            gap: 0;
        }

        > li {
            > a {
                height: var(--header-h);
                display: inline-flex;
                align-items: center;
            }
        }
    }

    .nav-cta {
        &:hover {
            background: transparent;
        }

        a {
            background: $light;
            color: $primary;
            height: auto;
            position: relative;
            top: 50%;
            transform: translateY(-50%);
            width: 120px;
            justify-content: center;
            border-radius: 4px;
            margin-left: 20px;

            &:hover {
                background: darken($light, 5%);
                color: $primary;
            }

            @include media-breakpoint-down(lg) {
                position: unset;
                transform: unset;
                margin: 5px 0 10px 15px;
                padding: 0;
                height: 40px;
            }
        }
    }

    .nav-link {
        color: #f0f0f0;
        font-weight: 600;
        transition: color 0.3s $easing;
        text-transform: uppercase;

        @include media-breakpoint-down(lg) {
            color: $primary;
        }

        &.active,
        &:hover {
            color: $light;

            @include media-breakpoint-down(lg) {
                color: $primary;
            }
        }
    }

    &-collapse {
        @include media-breakpoint-down(lg) {
            background: #fff;
        }

        @media (max-width: 376px) {
            position: absolute;
            width: 90%;
            top: var(--header-h);
            left: 50%;
            transform: translateX(-50%);
        }
    }
}

.nav-item {
    // position: relative;

    &:hover {
        background: #fff;

        .nav-link {
            color: $primary;
        }

        .megamenu {
            opacity: 1;
            transform: translateY(100%);
            pointer-events: all;

            @include media-breakpoint-down(lg) {
                transform: unset;
                opacity: 0;
            }
        }
    }

    @include media-breakpoint-up(lg) {
        .mega-toggle {
            display: none;
        }
    }

    @include media-breakpoint-down(lg) {
        position: relative;
        --header-h: 48px;
        // background: red;

        .nav-link {
            margin-left: 15px;
        }

        .mega-toggle {
            color: $primary;
            position: absolute;
            top: 0;
            right: 0;
            // transform: translateY(-50%);
            z-index: 1;
            height: var(--header-h);
            width: var(--header-h);
            @include flexCenter;
            transition: .5s $easing;

            > * {
                pointer-events: none;
            }

            &.active {
                transform: rotate(-180deg);

                + .megamenu {
                    max-height: 9999px;
                    padding: 25px;
                    opacity: 1;

                    .menu-block {
                        margin-left: 0;
                    }
                }
            }
        }
    }
}

.megamenu {
    position: absolute;
    background: #fff;
    // background: #fff var(--bgimg, none) no-repeat right bottom / auto calc(100% - 40px);
    left: 0;
    bottom: 1px;
    width: 100%;
    // width: 800px;
    // max-width: 100vw;
    min-height: 250px;
    transform: translateY(calc(100% + 20px));
    display: flex;
    // flex-flow: wrap;
    gap: 30px 50px;
    // padding: 25px;
    justify-content: center;
    opacity: 0;
    pointer-events: none;
    transition: .5s $easing;

    // @include media-breakpoint-up(lg) {
    //     left: -270px;
    // }

    @include media-breakpoint-down(lg) {
        position: unset;
        transform: unset;
        opacity: 0;
        pointer-events: unset;
        min-height: 0;
        max-height: 0;
        overflow: hidden;
        padding-top: 0;
        padding-bottom: 0;
        justify-content: flex-start;
    }

    .bgimg {
        background: #fff var(--bgimg, none) no-repeat center bottom / 100% calc(100% - 40px);
        width: 250px;
        transition: .3s $easing;
        margin-right: 25px;
        flex: 0 0 250px;

        @include media-breakpoint-down(lg) {
            display: none;
        }
    }

    .menu-block-wrap {
        display: flex;
        flex-flow: wrap;
        gap: 30px 50px;
        padding: 25px;

        @include media-breakpoint-down(lg) {
            // max-height: 0;
            // overflow: hidden;
            padding: 0;
            justify-content: flex-start;
        }
    }

    .menu-block {
        width: 200px;
        margin-left: 25px;

        @include media-breakpoint-up(lg) {
            margin-left: 0;
        }

        .item {
            padding: 0;
            margin: 0;
            list-style: none;
            // font-size: .85rem;
            text-transform: uppercase;

            li {
                &:not(:last-child) {
                    margin-bottom: 5px;
                }
            }

            &-title {
                font-size: 1.05rem;
                text-transform: uppercase;
                font-weight: 700;
                margin-bottom: 5px;
            }
        }
    }
}

.section {
    --spadding: 60px;
    padding-top: var(--spadding);
    padding-bottom: var(--spadding);
    position: relative;
    scroll-padding-top: calc(var(--header-h) + var(--topbar-h) + var(--spadding));
}

.section-head {
    color: $primary;

    .section-title {
        font-weight: 700;
    }

    &.alternate {
        display: flex;
        flex-direction: column-reverse;

        .section-title {
            margin: 10px 0 0;
        }

        .section-sub {
            text-transform: uppercase;
            color: $primary;
            font-weight: 700;
            color: $wlorange;
            letter-spacing: 2px;
        }
    }
}

.section-np {
    --spadding: 0;
}

.section-alt {
    background: $primary;
    color: #fff;

    .section-head {
        &.alternate {
            .section-title {
                color: #fff;
            }
        }
    }
}

.home-page {
    .section-hero {
        background: $wlnavy;
        color: #fff;
        --spadding: 40px;
        min-height: calc(100vh - 32px - var(--header-h) - env(safe-area-inset-top) - env(safe-area-inset-bottom));
        height: auto;

        .banner-img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100% !important;
            height: 100% !important;
        }

        .row {
            min-height: 60vh;
            align-items: center;
        }

        .intro {
            font-size: 1.8rem;
            text-transform: uppercase;

            @media (min-width: $bp-mdl) {
                font-size: 2.8rem;
            }

            strong {
                display: block;
                width: fit-content;
            }

            b,strong {
                // text-decoration: underline;
                border-bottom: 2px solid currentColor;
                color: $wlyellow;
            }
        }

        > .container {
            position: relative;
            z-index: 2;
        }
    }

    .trusted {
        // margin-top: 50px;
        background: rgba($light, .75);
        padding: 25px 0;
        // border-radius: 5px;

        .name {
            font-size: .85rem;
            font-weight: 700;
            color: $primary;
            margin-bottom: 10px;
            text-align: center;

            @media (min-width: $bp-mdxl) {
                text-align: left;
            }
        }

        img {
            max-width: 100%;
        }
    }
}

.shadow-container {
    position: absolute;
    top: 0;
    left: 0;
    @include wh100;
    overflow: hidden;
    text-align: right;
    z-index: 1;

    img,
    svg {
        @include wh100;
    }

    > .container {
        height: 100%;
    }
}

.shadow-wrap {
    margin-left: auto;
    height: 100%;
    position: relative;
    @include wh(100%);

    @media (min-width: $bp-mdl) {
        width: 40%;
    }

    @media (min-width: $bp-mdxl) {
        width: 50%;
    }

    @media (min-width: $bp-xl) {
        width: 60%;
    }

    img {
        // @include wh100;
        object-fit: contain !important;
        object-position: right !important;

        &:not(.shadow__body) {
            position: absolute;
            left: 0;
            z-index: -1;
        }
    }
}

.shadow__child {
    --tx: 0;
    --ty: 0;
    transform: translate(var(--tx), var(--ty));

    &:nth-child(1) {
        --tx: -3.69064px;
        --ty: 3.51617px;
    }

    &:nth-child(2) {
        --tx: -7.38128px;
        --ty: 7.03234px;
    }

    &:nth-child(3) {
        --tx: -11.0719px;
        --ty: 10.5485px;
    }

    &:nth-child(4) {
        --tx: -14.7626px;
        --ty: 14.0647px;
    }

    &:nth-child(5) {
        --tx: -18.4532px;
        --ty: 17.5809px;
    }

    &:nth-child(6) {
        --tx: -22.1439px;
        --ty: 21.097px;
    }
}

@import 'custom/scard';

.section-expertise {
    // background: url(../images/blurbg.jpg) no-repeat center / cover;
    color: #fff;
    overflow: hidden;
}

.exp-row {
    display: flex;
    position: relative;
    align-items: center;
    flex-direction: column;
    row-gap: 40px;

    @media (min-width: $bp-mdxl) {
        flex-direction: row;
    }
}

.exp-col {
    position: relative;
    width: 100%;

    @media (min-width: $bp-mdxl) {
        width: 450px;
        max-width: 100%;

        &::before {
            content: '';
            position: absolute;
            left: 50%;
            top: 50%;
            width: calc(100% + 80px);
            // height: calc(100% + 120px);
            height: 200%;
            transform: translate(-50%, -50%) skewX(10deg);
            background: rgba($primary, .75);
            z-index: 1;
            pointer-events: none;
        }
    }

    h2 {
        font-weight: 700;
        font-size: 2rem;
        z-index: 2;
        margin-bottom: 20px;
        position: relative;
    }

    + .tab-content {
        flex: 0 0 calc(50% - 80px);
        // background: #fff;
        // margin-left: auto;
        color: $primary;
        position: relative;
        // height: 250px;
        // padding: 30px;

        @media (min-width: $bp-mdxl) {
            margin-left: auto;
        }

        .tab-pane {
            // background: #fff;
            width: 100%;
            // height: 250px;
            // position: absolute;
            @include flexCenter;
            text-align: center;
            flex-direction: column;

            .ind-img {
                height: 200px;
                margin-bottom: -25px;
                margin-top: -60px;
                margin-inline: auto;

                @include media-breakpoint-up(lg) {
                    margin-top: -80px;
                }
            }

            &:not(.show) {
                display: none;
            }

            i {
                font-size: 35px;
            }

            svg {
                @include wh(35px);
            }

            h3 {
                margin: 0;
                font-size: 1.2rem;
                font-weight: 700;
                text-transform: uppercase;
                margin: 20px 0;
            }

            .tab-box {
                background: #fff;
                padding: 30px;
            }

            .cta-wrap {
                margin-top: 15px;
            }
        }
    }
}

#service {
    z-index: 2;
    position: relative;
    display: flex;
    flex-flow: nowrap;
    overflow-x: auto;

    @media (min-width: $bp-mdxl) {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }

    .nav-link {
        display: flex;
        flex-direction: column;
        background: transparent;
        color: #fff;
        justify-content: center;
        align-items: center;
        text-transform: uppercase;
        font-size: .8rem;
        gap: 10px;

        i {
            font-size: 50px;
        }

        svg {
            @include wh(50px);
        }

        &.active {
            color: $light;
        }
    }
}

.btn {
    font-weight: 700;
    transition: .3s $easing;

    &-light {
        &.btn-lg {
            padding: 10px 35px;
        }
    }

    &-primary {
        color: $light;

        &:hover {
            background: lighten($primary, 5%);
            color: $light;
        }
    }

    &-icon {
        display: inline-flex;
        align-items: center;


        > i,
        > svg {
            margin-left: 10px;
            position: relative;
            transform: translateX(-5px);
            transition: transform .3s $easing;
        }

        &:hover {
            > i,
            > svg {
                transform: unset;
            }
        }
    }
}

.pcard {
    background: $wlnavy;
    display: flex;
    flex-direction: column-reverse;
    color: $wlyellow;
    min-height: 400px;
    border-radius: 40px;
    transition: .5s $easing;

    @media (min-width: $bp-mdl) {
        flex-direction: row;
    }

    &:hover {
        transform: scale(1.05);
    }

    &:not(:last-child) {
        margin-bottom: 40px;
    }

    > * {
        flex: 0 0 50%;
    }

    &__img {
        .mediabox {
            width: 100% !important;

            @include media-breakpoint-down(md) {
                height: 280px !important;
            }
        }

        img {
            @include wh100;
            object-fit: cover;
            border-radius: 0 40px 40px 0;
        }
    }

    &__content {
        padding: 40px;
        display: flex;
        flex-direction: column;
        justify-content: center;

        .title {
            font-size: 2rem;
            font-weight: 700;
            margin-bottom: 20px;
        }

        .desc {
            color: #fff5eb;
        }

        .btn {
            color: #fff5eb;
            border-color: #fff5eb;
            margin-top: 20px;
            // border-radius: 4px;
            font-size: .8rem;
            padding: 10px 25px;
            width: fit-content;

            &:hover {
                color: $primary;
                border-color: $light;
                background: $light;
            }
        }
    }
}

.intg {
    &-wrapper {
        overflow: hidden;
        padding: 0 0 50px;
        display: flex;
    }

    &__list {
        margin: 0;
        padding: 0;
        list-style: none;
        // display: flex;
        // width: 200vw;

        // flex-flow: wrap;
        display: grid;
        grid-template-columns: repeat(10, 1fr);
        padding: 20px 0;
        gap: 20px;

        animation: 30s slide infinite linear forwards;

        li {
            margin-left: 80px;

            &:nth-child(7n+1) {
                margin: 10px 0 0 80px;
            }

            &:nth-child(7n+2) {
                margin: 70px 0 0 80px;
            }

            &:nth-child(7n+3) {
                margin: 30px 0 0 80px;
            }

            &:nth-child(7n+4) {
                margin: 80px 0 0 80px;
            }

            &:nth-child(7n+5) {
                margin: 60px 0 0 80px;
            }

            &:nth-child(7n+6) {
                margin: 20px 0 0 80px;
            }

            &:nth-child(7n+7) {
                margin: 80px 0 0 80px;
            }

        }
    }

    &__item {
        --shadow: 0 5px 15px 5px #{rgba($wlnavy, .1)};

        @include wh(80px);
        display: block;
        border-radius: 50%;
        background: #fff;
        box-shadow: var(--shadow);
        padding: 20%;
        position: relative;

        --tw-y: calc(100% + 10px);

        &:hover {
            --tw-y: calc(100% + 5px);

            .intg {
                &__name {
                    opacity: 1;
                    visibility: visible;
                }
            }
        }
    }

    &__icon {
        @include wh100;

        img {
            @include wh100;
        }
    }

    &__name {
        text-align: center;
        position: absolute;
        padding: 10px 20px;
        border-radius: 999px;
        background: #fff;
        box-shadow: var(--shadow);
        font-size: .95rem;
        white-space: nowrap;
        bottom: 0;
        left: 50%;
        transform: translate(-50%, var(--tw-y));
        opacity: 0;
        transition: .5s var(--easing);
        visibility: hidden;
    }
}

