.fab {
    &__wrap {
        position: fixed;
        right: 0;
        bottom: 0;
        z-index: 10;
        color: #fff;
        @include flexCenter;
        width: 100%;

        @media (min-width: $bp-mdl) {
            $gap: 20px;
            right: $gap;
            bottom: 40px;
            width: auto;
            flex-direction: column;
            gap: 10px;
        }
    }

    &__item {
        display: inline-flex;
        justify-content: center;
