.cursor {
    position: fixed;
    z-index: 9999999999;
    background: rgba($light, 1);
    @include wh(15px);
    margin-left: -10px;
    margin-top: -10px;
    border-radius: 50%;
    mix-blend-mode: difference;
    left: 0;
    top: 0;
    pointer-events: none;
    transition: transform 100ms $easing;

    &.hover {
        transform: scale(2);
    }

    @media (hover: none) {
        display: none;
    }
