                // border-bottom: 1px solid;
                // padding-bottom: 5px;
                font-weight: 600;
                font-size: 1.125rem;
            }

            &-list {
                counter-reset: top;

                ul {
                    margin: 0;
                    padding: 0;
                    list-style: none;
                    --margin-b: 1rem;

                    li {
                        counter-increment: top;

                        &.active {
                            &::before,
                            > a {
                                color: $wlorange;
                            }
                        }

                        &::before {
                            content: counter(top) ".";
                        }

                        &:not(:last-child) {
                            margin-bottom: var(--margin-b);
                        }

                        > ul {
                            padding-left: 1rem;
                            counter-reset: sub 1;
                            --margin-b: .5rem;

                            li {
                                counter-increment: sub;

                                &::before {
                                    content: counter(top) "." counter(sub) ".";
                                }

                                &::before,
                                a {
                                    color: #404040;
                                    font-weight: 600;
                                }
                            }
                        }
                    }
                }

                > ul {
                    > li {
                        &::before,
                        > a {
                            font-weight: 600;
                            font-size: 1.15rem;
                        }

                        > a {
                            margin-bottom: .5rem;
