                    float: unset !important;
                    margin-inline: auto;
                }
            }

            figure {
                margin: 0 auto 1.5rem;
                width: 800px;
                max-width: 100%;

                img {
                    margin: 0;
                    margin-left: auto;
                    margin-right: auto;
                }

                figcaption {
                    text-align: right;
                    font-style: italic;
                    font-size: 1rem;

                    @include media-breakpoint-down(md) {
                        font-size: .875rem;
                    }
                }
            }

            h1,h2,h3,h4,h5,h6 {
                margin-bottom: 1.25rem;
                font-weight: 700;
            }

            p {
                margin-bottom: 2rem;
                // font-size: 1.15rem;
                text-align: justify;

                &:last-child {
                    margin-bottom: 0;
                }

                // span {
                //     display: inline-block;
                // }
            }

            ul,ol {
                display: flex;
                flex-direction: column;
                gap: 4px;

                p {
                    &:last-child {
                        margin-bottom: 1rem;
                    }
