            .timeline-title {
                font-weight: 700;
                margin-bottom: 5px;
                font-size: 1.15rem;
            }
        }

        .timeline-content {
            width: 10rem;
            text-align: center;

            .inner-circle {
                border-radius: 1.5rem;
                height: 1rem;
                width: 1rem;
                display: inline-flex;
                align-items: center;
                justify-content: center;
                background-color: $primary;

                &:before {
                    content: "";
                    background-color: $primary;
                    display: inline-block;
                    height: 3rem;
                    width: 3rem;
                    min-width: 3rem;
                    border-radius: 6.25rem;
                    opacity: .5;
                }
            }
        }
    }
}

.cscard {
    position: relative;
    border-radius: 1rem;
    padding: 1.5rem;
    display: block;
    overflow: hidden;
    color: #fff;
    min-height: 400px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    transition: .3s $easing;

    &__img {
        position: absolute;
        z-index: 1;
        @include wh100;
        top: 0;
        left: 0;

        img {
            @include wh100;
            object-fit: cover;
            transition: .3s $easing;
        }

        &::before {
            position: absolute;
            content: " ";
            background: linear-gradient(to top, $primary, transparent);
            opacity: .95;
            @include wh100;
            top: 0;
            left: 0;
            z-index: 2;
        }
    }

    &__content {
        position: relative;
        z-index: 3;
        display: flex;
        flex-direction: column;
        gap: .5rem;
        transition: .3s $easing;
    }

    &__title {
        font-size: 1.5rem;
        font-weight: 700;
    }

    &__excerpt {
        @include lineClamp(2);
    }

    &:hover {
        .cscard {
            &__img {
                img {
                    transform: scale(1.05);
                }
            }

            &__content {
                color: $light;
            }
        }
    }
}

.trusted {
    img {
        filter: grayscale(1);
        transition: .5s $easing;

        &:hover {
            filter: grayscale(0);
        }
    }
}

.ticket-page {
    .section-hero {
        min-height: 0;
        height: 400px;
    }

    .footer-form {
        margin-top: 20px;
    }
}

.success {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    .icon {
        // @include wh(120px);
        // transform: scale(2.14);

        svg {
            margin-inline: auto;
        }
    }

    /* Courtesy of https://codepen.io/haniotis/pen/KwvYLO */

    // Misc
    $curve: cubic-bezier(0.650, 0.000, 0.450, 1.000);
    // $curve: $easing;

    .checkmark {
        width: 56px;
        height: 56px;
        border-radius: 50%;
        display: block;
        stroke-width: 2;
        stroke: $check-white;
        stroke-miterlimit: 10;
        box-shadow: inset 0px 0px 0px $check-green;
        animation: fill .4s ease-in-out .4s forwards, scale .3s ease-in-out .9s both;
    }

    .checkmark__circle {
        stroke-dasharray: 166;
        stroke-dashoffset: 166;
        stroke-width: 2;
        stroke-miterlimit: 10;
        stroke: $green;
        fill: none;
        animation: stroke .6s $curve forwards;
    }

    .checkmark__check {
        transform-origin: 50% 50%;
        stroke-dasharray: 48;
        stroke-dashoffset: 48;
        animation: stroke .3s $curve .8s forwards;
    }
}

.ticket-form {
    background: $primary;
    color: #fff;
}

.pagination {
    --bs-pagination-border-width: 0;
    --bs-pagination-border-radius: #{$border-radius};
    --bs-pagination-disabled-bg: #{mix($primary, #fff, 20%)};
    // --bs-pagination-bg: #{mix($primary, #fff, 30%)};
    --bs-pagination-hover-bg: #{mix($primary, #fff, 80%)};
    --bs-pagination-hover-color: #fff;
    gap: 8px;
    flex-flow: wrap;

    svg {
        @include wh(14px);
    }

    .page-item {
        @include flexCenter;
        @include wh(40px);
    }

    .page-link {
        @include wh100;
        @include flexCenter;
        border-radius: var(--bs-pagination-border-radius);
    }

    &-wrapper {
        &.center {
            .pagination {
                justify-content: center;
            }
        }
    }
}

