@use "sass:map";

$font-fallback: -apple-system,BlinkMacSystemFont,"Segoe UI","Roboto",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";

$easings: (
    "elastic": cubic-bezier(.19,1,.22,1),
    "material": cubic-bezier(.4,0,.2,1),
);

$easing: map.get($easings, "material");

$bp-xxxl: 1920px;
$bp-xxl: 1600px;
$bp-xl: 1366px;
$bp-lg: 1024px;
$bp-mdxl: 992px;
$bp-mdl: 768px;
$bp-md: 576px;

$lazy: ".mediabox";

$scrollbar-color: #bdbdbd;
$scrollbar-color-hover: darken($scrollbar-color, 20);

$wlnavy: #262e45;
$wlnavy-light: #37405a;
$wlyellow: #e3c2a0;
$wlteal: #2a8db2;
$wlorange: #e88612;

$fa-font-path: "../webfonts";

:root {
    --container: 8%;

    @media (min-width: 1600px) {
        --container: 18%;
    }

    @media (max-width: 1024px) {
        --container: 5%;
    }

    --header-h: 80px;
}

$primary: $wlnavy;
$light: $wlyellow;

$tooltip-bg: $primary;
$tooltip-color: $light;
$tooltip-margin: 5px;

$check-green: #7ac142;
$check-white: #fff;
