@mixin lineClamp($lines) {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: #{$lines};
    overflow: hidden;
}

@mixin wh100 {
    width: 100%;
    height: 100%;
}

@mixin wh($size) {
    width: #{$size};
    height: #{$size};
}

@mixin flexCenter() {
    display: flex;
    align-items: center;
    justify-content: center;
}

@mixin knockout() {
    @content;
    color: transparent;
    background-clip: text;
    -webkit-background-clip: text;
}

@mixin dropCap($font: '4rem', $margin: '0 15px 5px 0') {
    float: left;
    font-size: #{$font};
    line-height: 1;
    margin: #{$margin};
    font-weight: 600;
}
