.scard {
    // background: url(../images/services/appdev.jpg) no-repeat center / cover;
    background: $primary;
    height: 100%;
    color: #fff;
    padding: 25px;
    // min-height: 400px;
    height: 400px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative;

    // &::before {
    //     content: '';
    //     position: absolute;
    //     top: 0;
    //     left: 0;
    //     @include wh100;
    //     background: #000;
    //     opacity: .75;
    //     z-index: 1;
    // }
    &-row {
        > div {
            &:nth-child(even) {
                .scard {
                    background: $light;
                    color: $primary;

                    &__icon {
                        color: currentColor;
                    }

                    &__title {
                        color: currentColor;
                    }

                    &__tech {
                        a {
                            font-weight: 600;
                            color: currentColor;
                        }
                    }
                }
            }
        }
    }

    &__icon,
    &__title,
    &__desc,
    &__tech {
        z-index: 2;
        position: relative;
        transition: .5s $easing;
    }

    &__icon {
        @include wh(50px);
        font-size: 50px;
        // border: 1px solid #fff;
        color: $light;
    }

    &__title {
        margin: 20px 0 10px;
        text-transform: uppercase;
        color: $light;
        font-weight: 600;
        font-size: 21px;
    }

    &__tech {
        display: flex;
        gap: 10px;
        flex-flow: wrap;

        a {
            text-transform: uppercase;
            color: #fff;
            border-bottom: 1px solid;
            padding-bottom: 5px;
            text-decoration: none;
            font-size: .65rem;
        }
    }

    &__desc {
        max-height: 0;
        overflow: hidden;
    }

    &:hover {
        .scard {
            &__icon {
                transform: translateY(-70px);
                opacity: 0;
            }

            &__title,
            &__tech,
            &__desc {
                top: -35px;
                margin-top: 0;
            }

            &__desc {
                max-height: 1000px;
                top: -15px;
            }
        }
    }
}