.accordion {
    margin-top: 40px;

    &-head {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 15px;
        background-color: $primary;
        color: #fff5eb;
        // box-shadow: 0 3px 5px 0 rgba(#000, 0.08);
        // border-radius: 8px;
        font-size: 1rem;
        position: relative;
        z-index: 1;
        font-weight: 600;
        cursor: pointer;
        gap: 15px;

        .heading {
            line-height: 1.5;
        }
    }

    &-item {
        box-shadow: 0 3px 5px 0 rgba(#000, 0.08);
        border-radius: 5px;
        // min-height: 55px;
        // border: 1px solid red;
        overflow: hidden;

        &:not(:last-child) {
            margin-bottom: 20px;

            // @media (max-width: 1366px) {
            //     margin-bottom: 8px;
            // }
        }
    }

    &-content {
        // margin-top: -50px;
        // padding-top: 8px;
        background: #fff;
        // box-shadow: 0 2px 5px 1px rgba(#000,.08);
        padding: 0 20px;
        // border-radius: 0 0 15px 15px;
        transition: .25s $easing;
        overflow: hidden;
        // height: 0;
        opacity: 0;
        max-height: 0;
        line-height: 1.9;
        position: relative;
        // transform: scale(0);

        &::before {
            content: '';
            position: absolute;
            top: 0;
            width: calc(100% - 40px);
            height: 1px;
            background: #ebebeb;
            display: block;
            left: 0;
            right: 0;
            margin: 0 auto;
        }
    }

    &-toggle {
        transition: transform .25s $easing;
        font-size: 20px;
    }
}

.accordion-head.shown {
    + .accordion-content {
        opacity: 1;
        // height: auto;
        margin-top: 0;
        max-height: 1000px;
        padding: 20px;
        // transform: scale(1);
    }

    .accordion-toggle {
        transform: rotate(-180deg) scale(.6);
    }
}